import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { OptionsService } from '../../common/options.service';
import { gettext } from '../../i18n/gettext';
import { Cookie, CookieBannerSettings, CookiePreferences } from './cookie-banner.model';
@Injectable({
  providedIn: 'root'
})
export class CookieBannerService {
  readonly STORAGE_KEY: string = 'acceptCookieNotice';
  readonly COOKIE_BANNER = 'cookieBanner';
  isCookieBannerShowed$: Subject<boolean> = new Subject();

  private readonly cookieDescriptions = {
    required: gettext(
      "These cookies are required to enable core site functionality. They perform a task or operation without which a site's functionality would not be possible."
    ),
    functional: gettext(
      'These cookies are used to track site usage and to process my personal data to measure and improve usability and performance. We will neither forward this data to any third party nor use this data for direct marketing purposes. I recognize that I can opt in and out of these cookies at any time.'
    ),
    marketing: gettext('These cookies are used to target advertising to a user.')
  };

  constructor(private options: OptionsService) {}

  getCookiePreferencesConfig(): CookiePreferences {
    const settings = this.getCookieBannerSettings();
    return settings && settings.configuration;
  }

  getCookieBannerSettings(): CookieBannerSettings {
    return this.options.get(this.COOKIE_BANNER) || {};
  }

  setCookies(cookiePreferences: CookiePreferences) {
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(cookiePreferences));
  }

  shouldShowCookieBanner(): boolean {
    const settings = this.getCookieBannerSettings();
    return !!settings.cookieBannerText && !this.getUserCookiePreferences();
  }

  getUserCookiePreferences(): CookiePreferences {
    return JSON.parse(localStorage.getItem(this.STORAGE_KEY));
  }

  isConfigCookiePreferencesDefined() {
    return !!this.getCookiePreferencesConfig();
  }

  isFunctionalCookieDisabled() {
    const { functional }: CookiePreferences = this.getUserCookiePreferences() || {};
    /**
     * We want this check, as we should stop loading Gainsight tag only when functional cookie is explicitly set to false.
     * In the USA there is no need for the functional cookies when tracking users.
     * This is why we want to allow undefined to pass the check.
     * @ignore
     */
    return functional === false;
  }

  getCookieDescription(cookieType: string): string {
    return this.cookieDescriptions[cookieType];
  }

  transformCookiePreferencesToList(cookiePreferences: CookiePreferences): Cookie[] {
    return Object.entries(cookiePreferences).map(([name, value]) => {
      const isReadonly = name === 'required';
      return { name, value, isReadonly };
    });
  }

  transformCookiePreferencesListToCookiePreferences(
    cookiePreferencesList: Cookie[]
  ): CookiePreferences {
    return cookiePreferencesList.reduce((cookiePref: CookiePreferences, cookie: Cookie) => {
      const key: string = cookie.name;
      cookiePref[key] = cookie.value;

      return cookiePref;
    }, {});
  }
}
