import { Inject, Injectable, Injector, Optional } from '@angular/core';
import { CanActivate } from '@angular/router';
import { flatten } from 'lodash-es';
import { forkJoin, from, isObservable, Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { NavigatorNode, NavigatorNodeFactory } from '../navigator';
import { DynamicProviderConfig } from './model/dynamic-provider-config.model';
import { HOOK_DYNAMIC_PROVIDER_CONFIG } from './provider-configuration-hook';

@Injectable()
export class ProviderConfigurationNodeFactory implements NavigatorNodeFactory {
  private config: DynamicProviderConfig[];
  private nodes: NavigatorNode[];

  constructor(
    @Optional()
    @Inject(HOOK_DYNAMIC_PROVIDER_CONFIG)
    config: DynamicProviderConfig[][],
    private injector: Injector
  ) {
    this.config = flatten(config);
  }

  get() {
    if (!this.config || !this.config.length) {
      return;
    }

    if (!this.nodes) {
      const canActivate: Array<Observable<boolean>> = this.config
        .map(c =>
          c.navigation.canActivate && c.navigation.canActivate.length
            ? c.navigation.canActivate.map(ca => this.injector.get(ca))
            : undefined
        )
        .map(this.checkCanActivate.bind(this));

      return forkJoin(canActivate).pipe(
        map((canActivateResult: boolean[]) =>
          this.config
            .map((c, index) =>
              canActivateResult[index] ? new NavigatorNode(c.navigation) : undefined
            )
            .filter(el => !!el)
        ),
        tap(nodes => (this.nodes = nodes))
      );
    }

    return this.nodes;
  }

  private checkCanActivate(ca: CanActivate[]): Observable<boolean> {
    if (!!ca && ca.length) {
      const canActivateResult: Array<Observable<boolean>> = ca
        .map((canActivate: CanActivate) => canActivate.canActivate(undefined, undefined))
        .map(this.wrapIntoObservable.bind(this));

      return forkJoin(canActivateResult).pipe(
        map((caResult: boolean[]) => caResult.reduce((acc, curr) => acc && curr))
      );
    }
    return of(true);
  }

  private isPromise<T = any>(obj: any): obj is Promise<T> {
    return !!obj && typeof obj.then === 'function';
  }

  private wrapIntoObservable<T>(value: T | Promise<T> | Observable<T>): Observable<T> {
    if (isObservable(value)) {
      return value;
    }

    if (this.isPromise(value)) {
      return from(value);
    }

    return of(value);
  }
}
