import { Inject, Injectable, Optional } from '@angular/core';
import { flatten } from 'lodash-es';
import { RouteFactory } from '../router';
import { DynamicProviderConfig } from './model/dynamic-provider-config.model';
import { HOOK_DYNAMIC_PROVIDER_CONFIG } from './provider-configuration-hook';
import { ProviderConfigurationComponent } from './provider-configuration.component';

@Injectable()
export class ProviderConfigurationRouteFactory implements RouteFactory {
  private config: DynamicProviderConfig[];

  constructor(@Optional() @Inject(HOOK_DYNAMIC_PROVIDER_CONFIG) config: DynamicProviderConfig[][]) {
    this.config = flatten(config);
  }

  get() {
    return this.config.map(c => ({
      path: c.navigation.path,
      component: ProviderConfigurationComponent,
      canActivate: c.navigation.canActivate,
      data: {
        layout: c.layout,
        endpoint: c.endpoint
      }
    }));
  }
}
