import {
  DefaultLangChangeEvent,
  LangChangeEvent,
  TranslateService,
  TranslationChangeEvent
} from '@ngx-translate/core';
import {
  AfterViewChecked,
  ChangeDetectorRef,
  Directive,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy
} from '@angular/core';
import { Subscription } from 'rxjs';
import { isEqual } from 'lodash-es';

/**
 * This is temporary fallback directive that is used to handle HTML blocks translation on maintenance versions
 * in version 10.9.4 and later, this is replaced by default translate directive.
 */
@Directive({
  selector: '[forceHtmlTranslate]'
})
export class ForceHtmlTranslateDirective implements AfterViewChecked, OnDestroy {
  currentParams: any;
  onLangChangeSub: Subscription;
  onDefaultLangChangeSub: Subscription;
  onTranslationChangeSub: Subscription;

  /**
   * Used to trigger events when html is replaced by directive.
   */
  htmlUpdateEvent: EventEmitter<void> = new EventEmitter<void>();

  @Input() set translateParams(params: any) {
    if (!isEqual(this.currentParams, params)) {
      this.currentParams = params;
      this.checkNodes();
    }
  }

  constructor(
    private translateService: TranslateService,
    private element: ElementRef,
    private _ref: ChangeDetectorRef
  ) {
    // subscribe to onTranslationChange event, in case the translations of the current lang change
    if (!this.onTranslationChangeSub) {
      this.onTranslationChangeSub = this.translateService.onTranslationChange.subscribe(
        (event: TranslationChangeEvent) => {
          if (event.lang === this.translateService.currentLang) {
            this.checkNodes();
          }
        }
      );
    }

    // subscribe to onLangChange event, in case the language changes
    if (!this.onLangChangeSub) {
      this.onLangChangeSub = this.translateService.onLangChange.subscribe(
        (event: LangChangeEvent) => {
          this.checkNodes();
        }
      );
    }

    // subscribe to onDefaultLangChange event, in case the default language changes
    if (!this.onDefaultLangChangeSub) {
      this.onDefaultLangChangeSub = this.translateService.onDefaultLangChange.subscribe(
        (event: DefaultLangChangeEvent) => {
          this.checkNodes();
        }
      );
    }
  }

  ngAfterViewChecked() {
    this.checkNodes();
  }

  checkNodes() {
    if (this.isLookupKeyMissing()) {
      this.element.nativeElement.lookupKey = this.getLookupKey();
    }
    this.translateAndUpdateHtmlContent();
    this.htmlUpdateEvent.emit();
  }

  ngOnDestroy() {
    if (this.onLangChangeSub) {
      this.onLangChangeSub.unsubscribe();
    }

    if (this.onDefaultLangChangeSub) {
      this.onDefaultLangChangeSub.unsubscribe();
    }

    if (this.onTranslationChangeSub) {
      this.onTranslationChangeSub.unsubscribe();
    }
    this.htmlUpdateEvent.complete();
  }

  /**
   * Builds lookup key from innerHTML, removes comments (that might be added by Angular) and trims it.
   */
  private getLookupKey() {
    return this.element.nativeElement.innerHTML.replace(/<!--.*?-->/gs, '').trim();
  }

  private isLookupKeyMissing(): boolean {
    return !this.element.nativeElement.lookupKey;
  }

  private lookupKeyExist(): boolean {
    return this.element.nativeElement.lookupKey;
  }

  private translateAndUpdateHtmlContent(): void {
    if (this.lookupKeyExist()) {
      this.element.nativeElement.innerHTML = this.translateService.instant(
        this.element.nativeElement.lookupKey,
        this.currentParams || {}
      );
    }
  }
}
